@import "./sf/sf.css";
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: #808080;
  overflow: hidden;
  background-color: #333;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  display: block;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
  list-style-type: disc;
}
#react-weather {
  width: 100%;
  height: 100vh;
  background-size: 100% 100vh;
  background-repeat: no-repeat;
}
.current-location-yours {
  color: #e0e0e6b0;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.current-location-yours > span > img {
  width: 20px;
  object-fit: contain;
  margin-left: 10px;
  position: relative;
  top: 2px;
}
.current-location-info {
  width: inherit;
  height: calc(100vh - (38px * 2));
  max-height: calc(100vh - (38px * 2));
  padding: 0px 0px 20px 0px;
  position: relative;
  box-sizing: border-box;
  overflow-x: hidden;
}
.current-location-info-data {
  width: 100%;
  text-align: left;
  border-bottom: 1px dashed #ffffff70;
  margin-bottom: 15px;
}

.current-location-icon {
  width: 30px;
  object-fit: contain;
  position: relative;
  box-sizing: border-box;
  top: 10px;
}

.current-location-today > div:nth-child(1) > p {
  color: rgb(224, 224, 230);
  margin: 0px 0px 15px 0px;
}
.current-location-today > div:nth-child(1) > p:nth-child(1) {
  font-size: 18px;
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.5px;
  line-height: 1.5em;
  margin-bottom: 10px;
  margin-top: 5px;
}
.current-location-today > div:nth-child(1) > p:nth-child(3) {
  font-size: 10px;
  margin-top: 5px;
  font-family: "Quicksand";
  letter-spacing: 0.5px;
}
.current-location-today > div:nth-child(3) {
  display: inline-block;
}

.current-location-sky {
  color: #ffffffdb;
  width: auto;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  position: relative;
  top: -10px;
  margin-bottom: 15px;
}
.current-location-deg {
  font-size: 73px;
  font-family: "Product Sans Thin Regular", sans-serif;
  color: rgb(224, 224, 230);
  font-weight: 400;
  letter-spacing: 3px;
  position: relative;
  padding: 0px 10px 0px 0px;
  top: -5px;
}
.current-location-deg > span {
  font-size: 23px;
  position: absolute;
  top: 15px;
  color: rgb(224 224 230 / 85%);
}

.current-location-temp {
  text-align: left;
}
.current-location-weather {
  display: flex;
  color: rgb(224, 224, 230);
  box-sizing: border-box;
}

.current-location-temp > p > span {
  margin: 0 10px;
}
.current-location-wind {
  flex: 1;
  padding: 10px 10px 10px 0px;
}
.current-location-wind:nth-child(1) {
  text-align: left;
}
.current-location-wind:nth-child(2) {
  text-align: center;
  position: relative;
  left: -8px;
}
.current-location-wind:nth-child(3) {
  text-align: right;
  position: relative;
}
.current-location-wind:nth-child(4) {
  text-align: right;
  padding-right: 25px;
}
.current-location-wind > svg {
  color: #526e95;
}
.current-location-wind > span {
  top: -4px;
  position: relative;
  font-size: 11.2px;
  font-family: "Roboto";
  left: 10px;
}

.current-location-reports {
  width: inherit;
  height: auto;
  margin: 20px auto;
  display: block;
  text-align: left;
}
.current-location-report-type {
  padding: 10px 0px 10px 0px;
  margin: 10px 0;
  color: rgb(224, 224, 230);
  font-family: "segoe ui", sans-serif;
  font-size: 14px;
  cursor: pointer;
}
.current-location-report-type > p {
  position: relative;
}
.current-location-report-type > p > span {
  position: absolute;
  top: 3.5px;
  right: 0px;
}
.current-location-dhm-c::-webkit-scrollbar {
  display: none;
}
.current-location-dhm {
  width: 100%;
  height: inherit;
  padding: 0px 0px 10px 0px;
  z-index: 10;
  box-sizing: border-box;
  overflow: hidden;
  position: absolute;
  top: 0px;
  transition: all 500ms;
  left: 100%;
}
.current-location-dhm-c {
  width: 100%;
  height: inherit;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 10px;
}
.current-location-dhm-show {
  left: 0%;
}
.weather-app {
  width: 500px;
  height: 100vh;
  box-sizing: border-box;
  margin: 0px auto;
  display: block;
  position: relative;
}
.weather-main-app {
  width: inherit;
  height: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-sizing: border-box;
  position: relative;
  padding: 30px 20px 20px 20px;
  background-image: linear-gradient(#272727, #000);
}

.weather-info {
  width: 100%;
  height: auto;
  padding: 10px 0px 0px 0px;
  text-align: center;
  align-content: center;
  position: relative;
  box-sizing: border-box;
}
.weather-icon {
  width: 60px;
  object-fit: contain;
  animation: linear infinite alternate;
  animation-name: anim;
  animation-duration: 2s;
  margin-left: 70px;
}
.weather-search-loc-h {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  background-color: #000;
  opacity: 0.9;
  z-index: 9;
  transition: 200ms;
}
.weather-search {
  padding: 10px;
  margin-top: 10px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgb(72 72 74 / 0%);
  border-bottom: 0px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  outline: none;
  text-align: center;
  font-size: 15px;
  color: #e0e0e6b0;
  background-color: #1d1d1f;
  text-transform: capitalize;
  z-index: 10;
  margin-bottom: 20px;
  position: relative;
  transition: 300ms;
}
.current-location-search-d {
  width: 100%;
  height: auto;
  position: relative;
}
.current-location-report-back {
  color: #2997ff !important;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  text-align: left;
  left: -5px;
  position: relative;
  cursor: pointer;
}
.current-location-report-back > span {
  position: relative;
  top: -3px;
}
.weather-search::placeholder {
  color: #ffffffb0;
}

.location-result::-webkit-scrollbar {
  display: none;
}

.location-result {
  width: 100%;
  height: 0px;

  box-sizing: border-box;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  margin: 0px auto;
}
.current-location-data::-webkit-scrollbar {
  display: none;
}
.current-location-data {
  width: 100%;
  height: inherit;
  padding: 0px 0px 10px 0px;
  z-index: 10;
  overflow: auto;
  box-sizing: border-box;
  position: absolute;
  top: 0px;
  transition: all 500ms;
  left: 0%;
}
.weather-search-result {
  width: 100%;
  height: inherit;
  padding: 0px 0px 10px 0px;
  z-index: 10;
  overflow: hidden;
  box-sizing: border-box;
  position: absolute;
  top: 0px;
  transition: all 500ms;
  left: 100%;
}
.current-location-data-hide {
  left: -100%;
}
.weather-search-result-show-force {
  left: 100%;
}
.weather-search-result-show {
  left: 0%;
}
.weather-search-no-radius-bottom {
  margin-bottom: 5px;
}
.show-location-result {
  height: auto;
  padding: 0px 10px 10px 10px;
}
.location-result-gif {
  width: 30px;
  object-fit: contain;
}

.location-data {
  width: 100%;
  padding: 20px 0px 10px 0px;
  box-sizing: border-box;
}
.location-data:nth-child(1) {
  margin-top: 0px;
}
.location-data:last-child {
  margin-bottom: 30px;
}
.location-info {
  width: 100%;
  display: flex;
  list-style: none;
  cursor: pointer;
  vertical-align: middle;
}

.location-info-name {
  width: 500px;
  font-family: "Roboto";
  color: #ffffffd1;
  font-size: 13px;
  text-align: left;
  line-height: 1.5em;
  flex: 1;
}
.location-info-cflag {
  text-align: left;
  flex: 1;
}
.location-info-flag {
  width: 15px;
  object-fit: contain;
  margin-left: 10px;
}
.location-info-temp {
  font-family: "Roboto";
  color: #ffffffd1;
  font-size: 13px;
  position: relative;
  top: 2px;
  flex: 1;
  text-align: left;
}
.location-info-cicon {
  position: relative;
  flex: 1;
  width: auto;
  display: inline-block;
}
.location-info-icon {
  width: 45px;
  object-fit: contain;
  position: absolute;
  top: -12px;
  right: 0px;
}
.location-info-coordinates {
  font-family: "sf pro text";
  color: #48484a;
  font-size: 13px;
  position: relative;
  top: 2px;
  text-align: right;
  display: none;
  flex: 1;
}
.location-reports {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  display: flex;
  margin-top: 20px;
  flex-direction: row;
}
.location-reports-card {
  width: 100px;
  height: 130px;
  box-sizing: border-box;
  flex: 1 1 auto;
  margin-right: 10px;
  border-radius: 15px;
}
.location-reports-card:nth-child(1) {
  background-color: #f58a5b;
  border: 1px slid #f58a5b;
}
.location-reports-card:nth-child(5) {
  margin-right: 0px !important;
}

.location-info-name-report {
  width: 500px;
  font-family: "Roboto";
  color: #ffffffd1;
  font-size: 13px;
  text-align: left;
  line-height: 1.5em;
  flex: 1;
  text-transform: capitalize;
}
.location-info-cflag-report {
  text-align: left;
  flex: 1;
}
.location-info-flag-report {
  width: 15px;
  object-fit: contain;
  margin-left: 10px;
}
.location-info-temp-report {
  font-family: "Roboto";
  color: #ffffffd1;
  font-size: 13px;
  position: relative;
  top: 2px;
  flex: 1;
  text-align: left;
}
.location-info-cicon-report {
  position: relative;
  flex: 1;
  width: auto;
  display: inline-block;
  text-align: left;
}
.location-info-icon-report {
  width: 45px;
  object-fit: contain;
  position: absolute;
  top: -12px;
  left: 0px;
}
.location-info-coordinates-report {
  font-family: "Roboto", sans-serif;
  color: #ffffffd1;
  font-size: 13px;
  position: relative;
  top: 2px;
  text-align: center;
  flex: 1;
}

.progress_bar {
  position: absolute;
  height: 2px;
  width: 100%;
  overflow: hidden;
  z-index: 1000;
  top: 0px;
}

.progress_bar .indeterminate:before {
  content: "";
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  -webkit-animation: indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395)
    infinite;
  animation: indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
}
.progress_bar .indeterminate:after {
  content: "";
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  -webkit-animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1)
    infinite;
  animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1)
    infinite;
  -webkit-animation-delay: 1.15s;
  animation-delay: 1.15s;
}

@-webkit-keyframes indeterminate {
  0% {
    left: -35%;
    right: 100%;
  }
  60% {
    left: 100%;
    right: -90%;
  }
  100% {
    left: 100%;
    right: -90%;
  }
}
@keyframes indeterminate {
  0% {
    left: -35%;
    right: 100%;
  }
  60% {
    left: 100%;
    right: -90%;
  }
  100% {
    left: 100%;
    right: -90%;
  }
}
@-webkit-keyframes indeterminate-short {
  0% {
    left: -200%;
    right: 100%;
  }
  60% {
    left: 107%;
    right: -8%;
  }
  100% {
    left: 107%;
    right: -8%;
  }
}
@keyframes indeterminate-short {
  0% {
    left: -200%;
    right: 100%;
  }
  60% {
    left: 107%;
    right: -8%;
  }
  100% {
    left: 107%;
    right: -8%;
  }
}
@media screen and (max-width: 555px) {
  .weather-info {
    width: 100%;
    box-sizing: border-box;
  }
  .weather-app {
    width: 100%;
  }
  .current-location-weather {
    width: 100%;
    padding: 0px 0px;
  }
}
@media screen and (max-width: 320px) {
  .mbar {
    display: none;
  }
}
